.tooltip {
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  display: block;
  width: max-content;
  max-width: 232px;
  padding: 16px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 4px;
  position: absolute;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease 0.12s;
}

.tooltip.show {
  opacity: 1;
  pointer-events: all;
}

.tooltip::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 9px;
  background-image: url("../img/tiptail.svg");
}

.tooltip.top-left {
  bottom: calc(100% + 20px);
  left: -20px;
}

.tooltip.top-left::after {
  bottom: -9px;
  left: 25px;
}

.tooltip.top-center {
  bottom: calc(100% + 20px);
  left: 50%;
  transform: translateX(-50%);
}

.tooltip.top-center::after {
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip.top-right {
  bottom: calc(100% + 20px);
  right: -20px;
}

.tooltip.top-right::after {
  bottom: -9px;
  right: 25px;
}

.tooltip.right-top {
  top: -12px;
  left: calc(100% + 20px);
}

.tooltip.right-top::after {
  transform-origin: top left;
  transform: rotate(90deg);
  top: 16px;
  left: 0;
}

.tooltip.right-center {
  top: 50%;
  left: calc(100% + 20px);
  transform: translateY(-50%);
}

.tooltip.right-center::after {
  transform-origin: top left;
  transform: rotate(90deg);
  top: calc(50% - 7px);
  left: 0;
}

.tooltip.bottom-center {
  top: calc(100% + 20px);
  left: 50%;
  transform: translateX(-50%);
}

.tooltip.bottom-center::after {
  top: -9px;
  left: 50%;
  transform: translateX(-50%) rotate(180deg);
}

.tooltip.left-center {
  top: 50%;
  right: calc(100% + 20px);
  transform: translateY(-50%);
}

.tooltip.left-center::after {
  transform-origin: top right;
  transform: rotate(-90deg);
  top: calc(50% - 7px);
  right: 0;
}

.tooltip-positions__tip:hover .tooltip {
  opacity: 1;
  pointer-events: all;
}

.tooltip-permanent {
  position: relative;
  top: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  transform: none !important;
}

