.hint-types__hint:not(:last-child) {
  margin-bottom: 32px;
}

.hint {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  display: block;
  width: max-content;
  max-width: 288px;
  padding: 24px 40px 24px 24px;
  color: #151515;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  opacity: 0;
  pointer-events: none;
  box-shadow: 0px 4px 24px rgba(67, 72, 78, 0.2);
  transition: opacity 0.2s ease 0.12s;
}

.hint__close {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 24px;
  height: 24px;
  background-image: url("../img/icon-close_small.svg");
  background-size: cover;
  background-repeat: no-repeat;
}

.hint__close:hover {
  cursor: pointer;
}

.hint__link {
  text-decoration: none;
  color: #2858FF;
}

.hint.show {
  opacity: 1;
  pointer-events: all;
}

.hint::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 9px;
  background-image: url("../img/tiptail-white.svg");
}

.hint.top-left {
  bottom: calc(100% + 20px);
  left: -20px;
}

.hint.top-left::after {
  bottom: -9px;
  left: 25px;
}

.hint.top-center {
  bottom: calc(100% + 20px);
  left: 50%;
  transform: translateX(-50%);
}

.hint.top-center::after {
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
}

.hint.top-right {
  bottom: calc(100% + 20px);
  right: -20px;
}

.hint.top-right::after {
  bottom: -9px;
  right: 25px;
}

.hint.right-top {
  top: -20px;
  left: calc(100% + 20px);
}

.hint.right-top::after {
  transform-origin: top left;
  transform: rotate(90deg);
  top: 22px;
  left: 0;
}

.hint.right-center {
  top: 50%;
  left: calc(100% + 20px);
  transform: translateY(-50%);
}

.hint.right-center::after {
  transform-origin: top left;
  transform: rotate(90deg);
  top: calc(50% - 7px);
  left: 0;
}

.hint.bottom-center {
  top: calc(100% + 20px);
  left: 50%;
  transform: translateX(-50%);
}

.hint.bottom-center::after {
  top: -9px;
  left: 50%;
  transform: translateX(-50%) rotate(180deg);
}

.hint.left-center {
  top: 50%;
  right: calc(100% + 20px);
  transform: translateY(-50%);
}

.hint.left-center::after {
  transform-origin: top right;
  transform: rotate(-90deg);
  top: calc(50% - 7px);
  right: 0;
}

.hint.left-top {
  top: -20px;
  right: calc(100% + 20px);
}

.hint.left-top::after {
  transform-origin: top right;
  transform: rotate(-90deg);
  top: 22px;
  right: 0;
}

.hint-permanent {
  position: relative;
  top: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  transform: none !important;
}

