.single-slider,
.double-slider {
  height: 16px;
}

.single-slider .irs-line,
.double-slider .irs-line {
  top: 7px;
  height: 2px;
  background-color: #D1D8E1;
}

.single-slider .irs-bar,
.double-slider .irs-bar {
  top: 7px;
  height: 2px;
  background-color: #151515;
}

.single-slider .irs-handle,
.double-slider .irs-handle {
  top: 50%;
  width: 16px;
  height: 16px;
  background-color: #151515;
  border-radius: 50%;
  transform: translateY(-50%);
  transition: transform 0.2s;
}

.single-slider .irs-handle i:nth-child(n),
.double-slider .irs-handle i:nth-child(n) {
  display: none;
}

.single-slider .irs-handle:hover, .single-slider .irs-handle.hovered,
.double-slider .irs-handle:hover,
.double-slider .irs-handle.hovered {
  cursor: pointer;
  transform: translateY(-50%) scale(1.5);
}

.single-slider .irs-handle.hovered::before,
.double-slider .irs-handle.hovered::before {
  content: "";
  position: absolute;
  bottom: -12px;
  left: 0;
  width: 24px;
  height: 24px;
  background-image: url(../img/pointer.svg);
  background-size: contain;
  background-repeat: no-repeat;
  pointer-events: none;
}

.single-slider .irs-single,
.double-slider .irs-single {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  padding: 4px 8px;
  top: auto;
  bottom: calc(100% + 23px);
  background-color: #151515;
  border-radius: 4px;
}

.single-slider .irs-single::before,
.double-slider .irs-single::before {
  width: 14px;
  height: 9px;
  margin: 0;
  border: none;
  background-image: url("../img/tiptail-black.svg");
  bottom: -9px;
  transform: translateX(-50%);
}

.single-slider .irs-handle.focused::before, .single-slider .irs-handle.focused::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  border: 2px solid #fff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.single-slider .irs-handle.focused::before {
  width: calc(100% + 2px);
  height: calc(100% + 2px);
}

.single-slider .irs-handle.focused::after {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  border-color: #2858FF;
}

